.footer-section {
    background-color: #0d2436;
    padding: 1rem;
  }
  
  .footer-top {
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: space-between;
    padding: 0 2rem;
  }
  .footer-btm {
    text-align: center;
    color: #fff;
    padding: 0.5rem;
  }
  .footer-subhead {
    color: #878787;
    font-size: 1.3rem;
    font-weight: 500;
    margin-top: 9px;
  }
  .footer-sublist a {
    font-size: 1.1rem;
    line-height: 1.8;
    color: #fff;
    display: block;
    font-weight: 400;
    text-decoration: none;
  }
  